/*********************
REUSABLE ITEMS
*********************/
.clearfix, .cf {
  zoom: 1; }
  .clearfix:before, .clearfix:after, .cf:before, .cf:after {
    content: "";
    display: table; }
  .clearfix:after, .cf:after {
    clear: both; }

.modern-clearfix {
  display: flow-root; }

.screen-reader-text {
  position: absolute;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  word-wrap: normal !important;
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0; }
  .screen-reader-text:focus {
    z-index: 100000;
    /* Above WP toolbar. */
    clip: auto !important;
    clip-path: none;
    display: block;
    top: 5px;
    left: 5px;
    height: auto;
    width: auto;
    padding: 15px 23px 14px;
    font-size: 1em;
    line-height: normal;
    text-decoration: none;
    background-color: #fff;
    color: #547E2A; }

.scroll-overflow {
  overflow-x: hidden;
  overflow-y: auto;
  /* use hidden if you only want to show on hover */
  -ms-overflow-style: none;
  /* IE 10+ */
  scrollbar-width: thin;
  /* Firefox can also use 'none' */ }
  .scroll-overflow::-webkit-scrollbar {
    /* Safari and Chrome */
    width: 8px; }
  .scroll-overflow::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 1px 0 0 0 #f9f9f9;
    background: #eee; }
  .scroll-overflow::-webkit-scrollbar-thumb {
    border-radius: 8px;
    background: #333; }

.nowrap {
  display: inline-block; }

.hide {
  display: none; }

